<template>
  <div class="container" id="frameContainer">
      <div v-if="!trackNo"></div>
      <!-- <iframe v-else :src="`http://zhb-dev.qd-ctcc-rrs.haier.net/wl-map/?no=${trackNo}`" name="ac" frameborder="0" width="100%" scrolling="auto" id="ActiveIframe" class="activeiframe"></iframe> -->
      <iframe v-else :src="`https://zhb.rrswl.com/wl-map/?no=${trackNo}`" name="ac" frameborder="0" width="100%" scrolling="auto" id="ActiveIframe" class="activeiframe"></iframe>
  </div>
</template>
<script>
export default {
  data () {
    return {
      trackNo: '',
    }
  },
  created() {
    this.trackNo = this.$route.query.trackNo
  },
  mounted() {
    if (this.trackNo) {
      this.$nextTick(i => {
        let pageInfo = this.getBrowserInterfaceSize()
        document.getElementById('ActiveIframe').style.height = (pageInfo.pageHeight - 46) + 'px'
        document.getElementById('frameContainer').style.height = (pageInfo.pageHeight - 46) + 'px'
      })
    }
  },
  methods: {
    /**
     * @description 获取浏览器窗口宽高
     */
    getBrowserInterfaceSize() {
      var pageWidth = window.innerWidth;
      var pageHeight = window.innerHeight;
      if (typeof pageWidth !== 'number') {
        // 在标准模式下面
        if (document.compatMode === 'CSS1Compat') {
          pageWidth = document.documentElement.clientWidth;
          pageHeight = document.documentElement.clientHeight;
        } else {
          pageWidth = document.body.clientWidth;
          pageHeight = window.body.clientHeight;
        }
      }

      return {
        pageWidth: pageWidth,
        pageHeight: pageHeight
      }
    }
  },
}
</script>

<style lang="scss" scoped>
.activeiframe {
  height: calc(100vh - 1rem);
}
.container{
  height: calc(100% - 0.92rem);
}
</style>
